@import '../../variable.scss';
@import '../../mixins.scss';

.sidebar {
    display: grid;
    grid-template-columns: 1f 2fr;
    margin-top: 15px;

    .filter-heading {
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-20)/14px var(--unnamed-font-family-muli);
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--gray-d2);
        box-shadow: 0px 2px #f3efef;
        padding: 16px 0;
    }

    .border-line>a {
        color: var(--gray-d2);
    }

    .filter-section {
        margin: 10px 0;

        p {
            padding: 12px 0;
            font-weight: bold;
        }

        ul {
            padding: 0;

            li {
                font: var(--unnamed-font-style-normal) normal 600 var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--gray-d2);
                list-style-type: none;
                padding-bottom: 12px;

                input {
                    width: 24px;
                    height: 24px;
                    border-radius: 10%;
                }

                label {
                    margin-left: 10px;
                    vertical-align: super;
                }
            }

            p {
                font: var(--unnamed-font-style-normal) normal 600 var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
                letter-spacing: var(--unnamed-character-spacing-0);
                color: var(--gray-d2);
                padding-bottom: 17px;
            }


        }

        .border-line {
            box-shadow: 0px 2px #f3efef;

        }

    }


    .sidebar-colors-wrapper {
        div {
            padding: 12px 0;
        }

        p {
            font: var(--unnamed-font-style-normal) normal 600 var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
            letter-spacing: var(--unnamed-character-spacing-0);
            color: var(--gray-d2);
            padding-bottom: 17px;
        }

        .sidebar-colors-section {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr;
            grid-gap: 8px;
            grid-row-gap: 8px;


            div {
                width: 64px;
                height: 40px;
                border-radius: 6px;

            }

            .black-color {
                background: $color_black 0% 0% no-repeat padding-box;
            }

            .white-color {
                background: $color_white 0% 0% no-repeat padding-box;
                border: 2px solid #DFE1E2;
            }

            .teal-green {
                background: $color_teal-green ;
            }

            .mustard-color {
                background: $color_mustard 0% 0% no-repeat padding-box;
            }

            .blue-color {
                background: $color_blue 0% 0% no-repeat padding-box;
            }

            .red-color {
                background: $color_red 0% 0% no-repeat padding-box;
            }

            .lavender-color {
                background: $color_lavender 0% 0% no-repeat padding-box;
            }

            .pink-color {
                background: $color_pink 0% 0% no-repeat padding-box;
            }

            .dark-yellow {
                background: $color_yellow 0% 0% no-repeat padding-box;
            }

            .multi-color {
                background: transparent linear-gradient(180deg, #AF52FF 0%, #7950E8 16%, #4F5ADA 29%, #57CB76 43%, #EBC250 56%, #FF8810 69%, #E32F0F 83%, #BF1919 100%) 0% 0% no-repeat padding-box;
            }

        }

    }
}